import { S as Storage } from './storage.js';

class ThemeSelector {
    constructor() {
        this.currentTheme = 'dark';
        this.availableThemes = new Map();
        this.categoryOrder = [];
        this.loadedStylesheets = new Set();
        this.loadedFonts = new Set();
        this.singleWordTerms = new Set(['pac-man']);
        this._styleCache = new Map();
        this._previewStyles = new Map();
        this._themeLoadPromise = null;
        this._activeTheme = null;
        this._pendingThemeLoad = null;
        this._activeStyleElement = null;
        // Ya no inyectar los estilos en el constructor
        this._styleElement = document.createElement('style');
        this._themesStyleElement = document.createElement('style');
        this._themesStyleElement.id = 'theme-styles';

        // No acceder al DOM en el constructor
        this._themeGrid = null;
    }
    loadThemeData() {
        // Extraer datos de los temas desde el CSS
        this.themesData = this.extractThemesFromCSS();
        
        if (!this.themesData?.categories) {
            console.error(' No se encontraron datos de temas');
            return;
        }
        // Limpiar datos previos
        this.categoryOrder = [];
        this.availableThemes.clear();
        Object.entries(this.themesData.categories).forEach(([category, data]) => {
            this.categoryOrder.push(category);
            data.themes.forEach(theme => {
                this.availableThemes.set(theme.file.replace('.css', ''), theme.name);
            });
        });
        // Generar el grid solo si el elemento existe
        this._themeGrid = document.querySelector('.theme-grid');
        if (this._themeGrid) {
            this.generateThemeGrid();
        }
        logThemes({
            categoras: this.categoryOrder,
            temas: Array.from(this.availableThemes.entries())
        }, 'loadThemeData');
    }

    extractThemesFromCSS() {
        const themesData = {
            categories: {
                'basic': { themes: [] },
                'modern-games': { themes: [] },
                'retro-games': { themes: [] }
            }
        };

        // Buscar todas las reglas CSS que definen temas
        const styleSheets = Array.from(document.styleSheets);
        try {
            for (const sheet of styleSheets) {
                if (sheet.href && sheet.href.includes('styles.css')) {
                    const rules = Array.from(sheet.cssRules || []);
                    for (const rule of rules) {
                        if (rule.selectorText?.startsWith('[data-theme')) {
                            const themeName = rule.selectorText.match(/\[data-theme="([^"]+)"\]/)?.[1];
                            if (themeName) {
                                const cssText = rule.cssText || rule.style?.cssText;
                                
                                // Obtener nombre y categora
                                const nameMatch = cssText.match(/--theme-name:\s*['"]([^'"]+)['"]/);
                                const categoryMatch = cssText.match(/--theme-category:\s*['"]([^'"]+)['"]/);
                                
                                if (nameMatch && categoryMatch) {
                                    const displayName = nameMatch[1];
                                    const category = categoryMatch[1];
                                    
                                    // Asegurarnos que la categora existe
                                    if (!themesData.categories[category]) {
                                        themesData.categories[category] = { themes: [] };
                                    }
                                    
                                    // Agregar el tema a su categora
                                    themesData.categories[category].themes.push({
                                        name: displayName,
                                        file: themeName
                                    });
                                }
                            }
                        }
                    }
                }
            }
        } catch (error) {
            console.error('Error extrayendo temas del CSS:', error);
        }

        return themesData;
    }

    generateThemeGrid() {
        // Asegurar que tenemos acceso al elemento grid
        if (!this._themeGrid) {
            console.error(' Elemento theme-grid no encontrado');
            return;
        }
        this._themeGrid.innerHTML = '';

        Object.entries(this.themesData.categories).forEach(([category, data]) => {
            // Convertir nombre de categora a formato legible
            const displayName = category.split('-').map(word =>
                word.charAt(0).toUpperCase() + word.slice(1)
            ).join(' ');
            // Agregar encabezado de categora
            const categoryHeader = document.createElement('div');
            categoryHeader.className = 'theme-category';
            categoryHeader.textContent = displayName;
            this._themeGrid.appendChild(categoryHeader);
            // Agregar temas de la categora
            data.themes.forEach(theme => {
                const themeName = theme.file.replace('.css', '');
                const option = document.createElement('button');
                option.className = 'theme-option';
                option.dataset.theme = themeName;
                const wrapper = document.createElement('div');
                wrapper.className = 'preview-wrapper';
                const previewCell = document.createElement('div');
                previewCell.className = 'preview-cell';

                // Agregar texto de muestra
                const previewText = document.createElement('div');
                previewText.className = 'preview-text';
                previewText.textContent = 'A';
                previewCell.appendChild(previewText);

                const previewMark = document.createElement('div');
                previewMark.className = 'preview-mark';
                previewCell.appendChild(previewMark);
                const previewTitle = document.createElement('div');
                previewTitle.className = 'preview-title';
                previewTitle.textContent = theme.name;
                wrapper.appendChild(previewCell);
                wrapper.appendChild(previewTitle);
                option.appendChild(wrapper);
                // Agregar event listener
                option.addEventListener('click', () => this.setTheme(themeName));
                this._themeGrid.appendChild(option);
            });
        });
        // Agregar manejador para el botn de retorno
        const backButton = document.querySelector('.back-button');
        if (backButton) {
            backButton.addEventListener('click', () => {
                const themeModal = document.getElementById('theme-modal');
                const mainModal = document.getElementById('modal');
                if (themeModal && mainModal) {
                    themeModal.classList.remove('active');
                    mainModal.classList.add('active');
                }
            });
        }
    }
    async injectBaseStyles() {
        console.log(' Inyectando estilos base...');
        
        // Ya no necesitamos inyectar estilos base porque estn en el CSS
        // Solo asegurarnos que los temas estn cargados
        this.loadThemeData();
        
        // Esperar a que se procesen los estilos
        await new Promise(resolve => {
            requestAnimationFrame(() => setTimeout(resolve, 100));
        });
    }
    getThemeStyles() {
        if (!this.themesData?.categories) {
            console.error(' No hay datos de temas');
            return '';
        }
        // Ya no necesitamos generar estilos aqu
        return '';
    }

    async initialize() {
        console.group('[ThemeSelector] Inicializando');
        try {
            const savedTheme = await Storage.get(Storage.KEYS.THEME, 'dark');
            console.log('Tema guardado:', savedTheme);

            this._pendingTheme = savedTheme;

            // Inyectar estilos base y cargar datos despus que el DOM est listo
            await this.injectBaseStyles();
            this.loadThemeData();

            console.log(' ThemeSelector preparado');
            return true;
        } catch (error) {
            console.error(' Error:', error);
            document.documentElement.setAttribute('data-theme', 'dark');
            return false;
        } finally {
            console.groupEnd();
        }
    }
    // Nuevo mtodo para aplicar el tema pendiente
    async applyPendingTheme() {
        if (!this._pendingTheme) return;
        console.log(' Aplicando tema:', this._pendingTheme);

        try {
            // 1. Cargar fuentes
            await this.loadFontsForTheme(this._pendingTheme);

            // 2. Aplicar tema
            document.documentElement.setAttribute('data-theme', this._pendingTheme);
            this._activeTheme = this._pendingTheme;
            // 3. Esperar fuentes y dar tiempo extra
            await document.fonts.ready;
            await new Promise(resolve => setTimeout(resolve, 100));
            this._pendingTheme = null;
            console.log(' Tema aplicado');
        } catch (error) {
            console.error(' Error aplicando tema:', error);
        }
    }
    async parseFonts() {
        // Extraer todas las fuentes del documento en vez del CSS
        const fontUrls = new Set();
        document.querySelectorAll('link[rel="stylesheet"][href*="fonts.googleapis.com"]')
            .forEach(link => {
                fontUrls.add(link.href);
            });
        return Array.from(fontUrls);
    }
    async loadFontsForTheme(themeName) {
        try {
            // Extraer todas las fuentes del CSS
            const fonts = await this.parseFonts();

            // Crear los elementos link para cada fuente
            const links = fonts.map(fontUrl => {
                const link = document.createElement('link');
                link.rel = 'stylesheet';
                link.href = fontUrl;
                return link;
            });
            // Esperar a que todas las fuentes se carguen
            await Promise.all(
                links.map(link => {
                    return new Promise((resolve, reject) => {
                        link.onload = () => resolve();
                        link.onerror = () => reject();
                        document.head.appendChild(link);
                    });
                })
            );
            await document.fonts.ready;
            console.log(' Fuentes cargadas para el tema:', themeName);
        } catch (error) {
            console.error(' Error cargando fuentes:', error);
        }
    }
    async preloadFontsForTheme(themeName) {
        try {
            console.log(' Precargando fuentes para:', themeName);
            const fonts = await this.parseFonts();

            // Cargar fuentes directamente sin preload
            const links = fonts.map(fontUrl => {
                const link = document.createElement('link');
                link.rel = 'stylesheet';  // Usar stylesheet en vez de preload
                link.href = fontUrl;
                return link;
            });
            // Agregar links y esperar a que carguen
            await Promise.all(
                links.map(link => new Promise((resolve) => {
                    link.onload = resolve;
                    link.onerror = resolve; // No bloquear en error
                    document.head.appendChild(link);
                }))
            );
            // Esperar a que las fuentes estn listas
            await document.fonts.ready;
            console.log(' Fuentes cargadas');
        } catch (error) {
            console.error(' Error cargando fuentes:', error);
        }
    }
    async setTheme(themeName) {
        if (this._activeTheme === themeName) return true;
        try {
            console.group(' Cambiando tema a:', themeName);

            // 1. Aplicar tema inmediatamente
            document.documentElement.setAttribute('data-theme', themeName);

            // 2. Cargar fuentes del tema
            await this.loadFontsForTheme(themeName);
            await document.fonts.ready;

            // 3. Actualizar estado y guardar
            this._activeTheme = themeName;
            await Storage.setCurrentTheme(themeName);

            // 4. Notificar cambio
            window.dispatchEvent(new CustomEvent('themechange', {
                detail: { theme: themeName }
            }));
            console.log(' Tema aplicado');
            console.groupEnd();
            return true;
        } catch (error) {
            console.error(' Error aplicando tema:', error);
            console.groupEnd();
            return false;
        }
    }
}
// Evitar logs duplicados
const loggedThemes = new Set();
function logThemes(themes, source) {
    const key = `${source}-${JSON.stringify(themes)}`;
    if (!loggedThemes.has(key)) {
        console.log(' Temas cargados:', themes);
        loggedThemes.add(key);
    }
}
export { ThemeSelector as T };