class TitleFitter {
    constructor(titleElement) {
        this.titleElement = titleElement;
        this.originalSize = parseFloat(getComputedStyle(titleElement).fontSize);
        
        this.fit = this.fit.bind(this);
        this.observe();
        this.bindEvents();
    }

    observe() {
        this.observer = new MutationObserver(this.fit);
        this.observer.observe(this.titleElement, {
            childList: true,
            characterData: true,
            subtree: true
        });
    }

    bindEvents() {
        window.addEventListener('resize', () => {
            requestAnimationFrame(this.fit);
        });
    }

    fit() {
        const title = this.titleElement;
        const container = title.parentElement;
        const padding = 30;
        
        // Resetear al tamao original
        title.style.fontSize = `${this.originalSize}px`;
        
        // Calcular y aplicar nueva escala
        const scale = Math.min(1, (container.clientWidth - padding) / title.scrollWidth);
        title.style.fontSize = `${this.originalSize * scale}px`;
    }

    destroy() {
        this.observer.disconnect();
    }
}

export { TitleFitter as T };
