import { Capacitor } from '@capacitor/core';
import { LocalNotifications } from '@capacitor/local-notifications';
import { App } from '@capacitor/app';
import { Haptics, ImpactStyle, NotificationType } from '@capacitor/haptics';
import { Preferences } from '@capacitor/preferences';
import { StatusBar, Style } from '@capacitor/status-bar';

// Eliminar import del splash screen

class NativeServices {
    static #isInitialized = false;
    static #serviceWorkerRegistration = null;
    static #isNativePlatform = false;

    // Nuevo mtodo especfico para SW
    static async initializeServiceWorker() {
        if (Capacitor.isNative) {
            console.log('Plataforma nativa, omitiendo SW');
            return true;
        }

        console.group(' Inicializando Service Worker');
        try {
            // 1. Limpiar SW antiguos
            await this.cleanupServiceWorker();

            // Usar ruta relativa para el SW
            this.#serviceWorkerRegistration = await navigator.serviceWorker.register(
                'sw.js',
                { scope: './' }
            );

            // 3. Esperar a que est activo
            await navigator.serviceWorker.ready;
            
            // 4. Solicitar permisos de notificacin
            if ('Notification' in window) {
                const permission = await Notification.requestPermission();
                console.log('Permisos de notificacin:', permission);
            }

            console.log(' Service Worker inicializado');
            console.groupEnd();
            return true;
        } catch (error) {
            console.error(' Error inicializando SW:', error);
            console.groupEnd();
            return false;
        }
    }

    static async initialize() {
        console.group(' Inicializacin de GameSalad');
        console.time('initialization');
        
        try {
            // Log estado inicial
            console.log('Estado inicial:', {
                readyState: document.readyState,
                fontsLoaded: document.fonts.check('1em "Rubik"'),
                viewport: {
                    width: window.innerWidth,
                    height: window.innerHeight
                }
            });

            // Inicializar Storage
            await this.initializeStorage();

            // Configurar servicios nativos si es necesario
            if (Capacitor.isNative) {
                await this.setupNativeServices();
            }

            this.#isInitialized = true;
            console.timeEnd('initialization');
            return true;
        } catch (error) {
            console.error('[Capacitor]  Error:', error);
            return false;
        } finally {
            console.groupEnd();
        }
    }

    static async cleanupServiceWorker() {
        if ('serviceWorker' in navigator) {
            try {
                // 1. Obtener todos los registros
                const registrations = await navigator.serviceWorker.getRegistrations();

                // 2. Desregistrar cada uno
                await Promise.all(
                    registrations.map(registration => {
                        console.log('Desregistrando SW:', registration.scope);
                        return registration.unregister();
                    })
                );

                // 3. Limpiar caches
                const cacheNames = await caches.keys();
                await Promise.all(
                    cacheNames.map(cacheName => {
                        console.log('Eliminando cach:', cacheName);
                        return caches.delete(cacheName);
                    })
                );

                console.log(' Service Workers y caches limpiados');
            } catch (error) {
                console.warn('Error limpiando SW:', error);
            }
        }
    }

    static async setupNativeServices() {
        console.group('[Capacitor] Configurando servicios nativos');

        // Verificar cada plugin
        const plugins = [
            { name: 'App', instance: App },
            { name: 'Haptics', instance: Haptics },
            { name: 'LocalNotifications', instance: LocalNotifications },
            { name: 'Preferences', instance: Preferences }
        ];

        for (const { name, instance } of plugins) {
            try {
                const available = Capacitor.isPluginAvailable(name);
                console.log(`- ${name}: ${available ? '' : ''}`);
                if (available && instance) {
                    // Intentar un mtodo simple para verificar funcionamiento
                    if (name === 'LocalNotifications') {
                        const perms = await instance.checkPermissions();
                        console.log(`  Permisos: ${perms.display}`);
                    }
                }
            } catch (err) {
                console.warn(` Error verificando ${name}:`, err);
            }
        }

        try {
            if (Capacitor.getPlatform() === 'ios') {
                await StatusBar.setOverlaysWebView(true);
                await StatusBar.setStyle({ style: Style.Light });
            } else {
                await StatusBar.setOverlaysWebView(false);
                await StatusBar.setBackgroundColor({ color: '#000000' });
                await StatusBar.setStyle({ style: Style.Dark });
            }
        } catch (error) {
            console.warn('[Capacitor] No se pudo configurar StatusBar:', error);
        }

        // Configurar listeners
        this.setupAppListeners();

        console.log(' Configuracin nativa completada');
        console.groupEnd();
    }

    static async setupWebNotifications() {
        if ('Notification' in window) {
            const permission = await Notification.requestPermission();
            console.log('Permisos de notificacin web:', permission);
        }
    }

    static async setupServiceWorker() {
        if (this.#isInitialized) return;

        try {
            this.setupAppListeners();

            // Usar isNative aqu tambin
            this.#isNativePlatform = Capacitor.isNative;
            console.log('Plataforma nativa:', this.#isNativePlatform);

            // Registrar Service Worker
            if ('serviceWorker' in navigator) {
                try {
                    const isLocalDevelopment =
                        window.location.hostname === 'localhost' ||
                        window.location.hostname.includes('192.168.') ||
                        window.location.hostname.includes('127.0.0.1');

                    // En desarrollo local, solo registrar si estamos en HTTP
                    if (!isLocalDevelopment || window.location.protocol === 'http:') {
                        this.#serviceWorkerRegistration = await navigator.serviceWorker.register(
                            '/sw.js',
                            {
                                scope: '/',
                                type: 'classic',
                                updateViaCache: 'none'
                            }
                        );
                        console.log(' Service Worker registrado:', this.#serviceWorkerRegistration);
                    } else {
                        console.log('Service Worker no registrado en desarrollo local');
                    }
                } catch (error) {
                    console.warn('Service Worker no disponible:', error);
                }
            }

            this.#isInitialized = true;
            console.log(' Servicios nativos inicializados');
        } catch (error) {
            console.error(' Error inicializando servicios nativos:', error);
        }
    }

    static setupAppListeners() {
        App.addListener('backButton', ({ canGoBack }) => {
            if (!canGoBack) {
                App.exitApp();
            }
        });

        App.addListener('appStateChange', ({ isActive }) => {
            if (!isActive) {
                document.dispatchEvent(new Event('appPause'));
            } else {
                document.dispatchEvent(new Event('appResume'));
            }
        });
    }

    static async initializeNotifications() {
        console.group(' Inicializando sistema de notificaciones');

        // 1. En plataformas nativas, usar Capacitor
        if (this.#isNativePlatform) {
            try {
                console.log(' Intentando usar notificaciones nativas...');
                const { display } = await LocalNotifications.checkPermissions();
                console.log('Estado de permisos nativos:', display);

                if (display === 'granted' || display === 'prompt') {
                    const perms = await LocalNotifications.requestPermissions();
                    console.log('Nuevos permisos nativos:', perms.display);

                    if (perms.display === 'granted') {
                        console.log(' Notificaciones nativas activadas');
                        console.groupEnd();
                        return;
                    }
                }
            } catch (error) {
                console.warn(' Notificaciones nativas no disponibles:', error);
            }
        }

        // 2. Verificar y solicitar permisos explcitamente
        if ('Notification' in window) {
            try {
                let permission = Notification.permission;

                if (permission === 'default') {
                    console.log(' Solicitando permisos de notificacin...');
                    permission = await Notification.requestPermission();
                }

                if (permission === 'granted') {
                    // Priorizar Service Worker si est disponible
                    if (this.#serviceWorkerRegistration?.active) {
                        console.log(' Service Worker notifications activadas');
                    } else {
                        console.log(' Web Notifications activadas');
                    }
                    console.groupEnd();
                    return;
                } else {
                    console.warn(' Permisos de notificacin denegados:', permission);
                }
            } catch (error) {
                console.warn(' Error solicitando permisos:', error);
            }
        }

        console.warn(' No hay sistema de notificaciones disponible');
        console.groupEnd();
    }

    static async vibrate(style = 'MEDIUM') {
        try {
            switch (style.toUpperCase()) {
                case 'LIGHT':
                    await Haptics.impact({ style: ImpactStyle.Light });
                    break;
                case 'HEAVY':
                    await Haptics.notification({ type: NotificationType.Success });
                    break;
                case 'MEDIUM':
                default:
                    await Haptics.impact({ style: ImpactStyle.Medium });
            }
            return true;
        } catch {
            console.warn('Haptics no disponible');
            return false;
        }
    }

    static getIconPath() {
        console.group(' Obteniendo rutas de iconos');
        const platform = Capacitor.getPlatform();

        let iconPath;
        if (platform === 'android') {
            iconPath = 'ic_notification';
        } else if (platform === 'ios') {
            iconPath = 'AppIcon';
        } else {
            iconPath = './assets/images/icon.png';
        }

        console.log('Plataforma:', platform);
        console.log('Icono seleccionado:', iconPath);
        console.groupEnd();
        return iconPath;
    }

    static getBasePath() {
        // Obtener la ruta base desde la etiqueta base o calcularla
        const baseElement = document.querySelector('base');
        if (baseElement?.href) {
            const url = new URL(baseElement.href);
            return url.pathname;
        }
        // Fallback: calcular desde la ruta actual
        const path = window.location.pathname;
        return path.substring(0, path.lastIndexOf('/') + 1);
    }

    static async sendNotification(title, body) {
        console.group(' Enviando notificacin');
        try {
            const platform = Capacitor.getPlatform();
            console.log('Plataforma detectada:', platform);

            if (platform === 'android' || platform === 'ios') {
                console.log(`Usando notificaciones nativas (${platform})`);

                const permResult = await LocalNotifications.requestPermissions();
                console.log('Permisos:', permResult);

                if (permResult.display !== 'granted') {
                    throw new Error(`Permiso denegado en ${platform}`);
                }

                // Programar para 2 segundos en el futuro
                const scheduleTime = new Date(Date.now() + 2000);

                const notificationConfig = {
                    title,
                    body,
                    id: Math.floor(Date.now() / 1000),
                    schedule: {
                        at: scheduleTime,
                        allowWhileIdle: true
                    },
                    autoCancel: true,
                    smallIcon: platform === 'android' ? 'ic_notification' : 'AppIcon',
                    android: {
                        channelId: 'default',
                        importance: 4,
                        priority: 3,
                        visibility: 1,
                        smallIcon: 'ic_notification'
                    }
                };

                await LocalNotifications.schedule({
                    notifications: [notificationConfig]
                });

                console.log(` Notificacin ${platform} programada para:`, scheduleTime);
                return true;
            }

            // Verificar que window y Notification existan
            if (typeof window === 'undefined' || typeof window.Notification === 'undefined') {
                console.warn(' API Notification no est disponible');
                throw new Error('Notificaciones web no soportadas');
            }

            // Asegurar que Notification existe antes de llamar requestPermission
            if ((platform !== 'android' && platform !== 'ios') && !('Notification' in window)) {
                console.warn(' API Notification no est disponible en este entorno');
                throw new Error('Notificaciones web no soportadas');
            }

            // Si no estamos en plataforma nativa, intentar usar notificaciones web
            if ('Notification' in window && 'serviceWorker' in navigator) {
                console.log('Usando notificaciones web');
                const permission = await Notification.requestPermission();

                if (permission === 'granted') {
                    const registration = await navigator.serviceWorker.ready;
                    await registration.showNotification(title, {
                        body,
                        icon: 'assets/images/icon.png', // Usar ruta relativa
                        vibrate: [200, 100, 200],
                        requireInteraction: true,
                        data: {
                            url: '.' // Usar ruta relativa al directorio actual
                        }
                    });

                    console.log(' Notificacin web enviada');
                    return true;
                }

                throw new Error('Permiso de notificacin denegado');
            }

            throw new Error('Sistema de notificaciones no disponible');

        } catch (error) {
            console.error(' Error en notificacin:', error);
            throw error;
        } finally {
            console.groupEnd();
        }
    }

    // Eliminar mtodo scheduleReminder ya que no se usa

    static async scheduleNativeNotification(title, body, hours) {
        console.group(' Programando notificacin nativa');
        try {
            console.log('Cancelando notificaciones anteriores...');
            await LocalNotifications.cancel({ notifications: [{ id: 1 }] });

            const scheduledTime = new Date(Date.now() + hours * 60 * 60 * 1000);
            console.log('Tiempo programado:', scheduledTime);

            await LocalNotifications.schedule({
                notifications: [{
                    title,
                    body,
                    id: 1,
                    schedule: {
                        at: scheduledTime,
                        allowWhileIdle: true
                    },
                    actionTypeId: 'OPEN_APP'
                }]
            });

            console.log(' Notificacin nativa programada');
            console.groupEnd();
            return true;
        } catch (error) {
            console.error(' Error en notificacin nativa:', error);
            console.groupEnd();
            return false;
        }
    }

    // Agregar mtodo helper para detectar mviles
    static isMobile() {
        return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ||
            ('maxTouchPoints' in navigator && navigator.maxTouchPoints > 0);
    }

    static async scheduleMobileNotification(title, body) {
        console.group(' Programando Service Worker notification');
        try {
            const registration = await navigator.serviceWorker.ready;
            console.log('Service Worker listo');

            await registration.showNotification(title, {
                body,
                icon: './assets/images/icon.png',  // Cambiar a ruta relativa
                vibrate: [200, 100, 200]
            });

            console.log(' Service Worker notification mostrada');
            console.groupEnd();
            return true;
        } catch (error) {
            console.error(' Error en Service Worker notification:', error);
            console.groupEnd();
            return false;
        }
    }

    static async saveData(key, value) {
        try {
            await Preferences.set({
                key,
                value: JSON.stringify(value)
            });
            return true;
        } catch {
            console.warn('Error guardando en Preferences, usando localStorage');
            localStorage.setItem(key, JSON.stringify(value));
            return false;
        }
    }

    static async getData(key, defaultValue = null) {
        try {
            const { value } = await Preferences.get({ key });
            return value ? JSON.parse(value) : defaultValue;
        } catch {
            console.warn('Error leyendo de Preferences, usando localStorage');
            const value = localStorage.getItem(key);
            return value ? JSON.parse(value) : defaultValue;
        }
    }

    static async removeData(key) {
        try {
            await Preferences.remove({ key });
            return true;
        } catch {
            localStorage.removeItem(key);
            return false;
        }
    }

    static async clearData() {
        try {
            await Preferences.clear();
            return true;
        } catch {
            localStorage.clear();
            return false;
        }
    }

    static async logToNative(message, data = {}) {
        // Evitar logs redundantes usando un Set o Map para trackear mensajes recientes
        if (!this._recentLogs) this._recentLogs = new Set();

        const key = `${message}-${JSON.stringify(data)}`;
        if (this._recentLogs.has(key)) return;

        this._recentLogs.add(key);
        setTimeout(() => this._recentLogs.delete(key), 1000); // Limpiar despus de 1 segundo

        if (Capacitor.getPlatform() === 'android') {
            console.debug(`[GameSalad] ${message}`, JSON.stringify(data, null, 2));
        } else {
            console.log(message, data);
        }
    }

    static async initializeStorage() {
        try {
            // Verificar acceso a Storage
            if (Capacitor.isNative) {
                // En nativo, verificar Preferences
                const testKey = '_test_storage_';
                await Preferences.set({ key: testKey, value: 'test' });
                await Preferences.remove({ key: testKey });
            } else {
                // En web, verificar localStorage
                const testKey = '_test_storage_';
                localStorage.setItem(testKey, 'test');
                localStorage.removeItem(testKey);
            }
            
            // Pre-cargar datos crticos
            const settings = {
                vibrationEnabled: await this.getData('vibrationEnabled', true),
                notificationEnabled: await this.getData('notificationEnabled', true)
            };
            
            console.log(' Storage inicializado:', settings);
            return settings;
        } catch (error) {
            console.error(' Error inicializando Storage:', error);
            return {
                vibrationEnabled: true,
                notificationEnabled: true
            };
        }
    }

    static async adjustTitle(texto, contenedor, disponible, anchoActual) {
        const timestamp = (performance.now() / 1000).toFixed(3);
        console.log(` [${timestamp}] Ajustando ttulo:`, { texto, contenedor, disponible, anchoActual });
        
        // Si es el ltimo ttulo (VG Characters)
        const isFinalTitle = texto === 'VG Characters';
        
        if (!texto || anchoActual <= disponible) {
            console.log(` [${timestamp}] No necesita ajuste`);
            
            if (isFinalTitle) {
                document.dispatchEvent(new CustomEvent('title-adjusted', {
                    detail: { 
                        timestamp,
                        texto,
                        anchoActual,
                        disponible,
                        final: true
                    }
                }));
            }
            
            return true;
        }

        // ...resto del cdigo de ajuste...

        document.dispatchEvent(new CustomEvent('title-adjusted', {
            detail: { 
                timestamp,
                texto,
                anchoActual,
                disponible,
                needed: true
            }
        }));
    }
}

export { NativeServices as N };
